.skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.skills_container {
  grid-template-columns: repeat(2, 350px);
  column-gap: 4rem;
  justify-content: center;
}

.skills_content {
  background-color: var(--container-color);
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 2rem 1rem;
  border-radius: 1.25rem;
  box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.1);
  width: 400px;
}

.skills_title {
  /* font-size: var(--normal-font-size); */
  font-size: 20px;
  font-weight: var(--font-medium);
  text-align: center;
  margin-bottom: var(--mb-1-5);
  color: blueviolet;
}

.skills_box {
  display: flex;
  justify-content: center;
  column-gap: 2.5rem;
}

.skills_group {
  display: grid;
  align-items: flex-start;
  row-gap: 1rem;
}

.skills_data {
  display: flex;
  column-gap: 1rem;
  margin-top: 25px;
}

.skills.bx-badge-check {
  font-size: 1rem;
  color: var(--title-color);
}

.skills_name {
  font-size: var(--normal-font-size);
  font-weight: var(--font-medium);
  line-height: 18px;
}

.skills_level {
  font-size: var(--tiny-font-size);
}

#express {
  width: 15px;
  height: 13px;
  color: black;
}

.bxl-nodejs {
  color: rgb(9, 128, 35);
}

.bxl-mongodb {
  color: green;
}

.bxl-firebase {
  color: rgb(255, 166, 0);
}

.bxl-react {
  color: blue;
}

.bxl-javascript {
  color: rgb(152, 152, 14);
}

.bxl-bootstrap {
  color: rgb(153, 78, 228);
}

.bxl-git {
  color: orangered;
}

.bxs-file-html {
  color: rgb(10, 7, 2);
}

.bxs-file-css {
  color: blue;
}

/* ==========break points========== */

@media screen and (max-width: 992px) {
  .skills_container {
    grid-template-columns: max-content;
    row-gap: 2rem;
  }
}

@media screen and (max-width: 768px) {
}

@media screen and (max-width: 576px) {
  .skills_container {
    grid-template-columns: 1fr;
  }

  .skills_content {
    background-color: var(--container-color);
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 2rem 1rem;
    border-radius: 1.25rem;
    box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.1);
    width: 400px;
  }
}

@media screen and (max-width: 350px) {
  .skills_box {
    column-gap: 1.25rem;
  }
  .skills_name {
    font-size: var(--small-font-size);
  }
}
